* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.containers {

    width: 100%;
    height: 150px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    padding: 10px;
    border-top: 1px solid rgba(0, 0, 0, 0.317);

}

.boxImg {


    height: 120px;
    width: 120px;
    border-radius: 10px;
    background-size: cover;
}

.img {
    position: relative;
    width: 100%;
    height: 100%;
}

.nombreProducto {
    font-size: 20px;
    margin-bottom: 3px;
}

.precioUnitario {
    font-size: 12px;
    color: rgba(0, 0, 0, 0.258);
}

.info {

    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 10px;

}

.upInfo {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 10px;

}

.precioDisponible {
    display: grid;
    grid-template-columns: .5fr 1fr;

}

.disponible {
    color: green;
}

.noDisponible {
    color: red;
}

input[type=number]::-webkit-inner-spin-button {
    opacity: 1
}

.downInfo {
    display: flex;
    justify-content: space-between;
}

.buttonDelete {
    background-color: #f4f4f4;
    width: 100px;
    height: 25px;
    margin-right: 20px;
    border: none;
    border-radius: 5px;

}

.buttonDelete:hover {
    background-color: rgb(232, 232, 232);

}

.buttonSave {
    background-color: #f4f4f4;
    width: 100px;
    height: 25px;
    border: none;
    border-radius: 5px;
}

.buttonSave:hover {
    background-color: rgb(232, 232, 232);

}

.cantidad {
    border: 1px solid rgba(0, 0, 0, 0.074);
    border-radius: 5px;
    padding: 5px;
    outline: none;
    width: 50px;

}